.create-campaign-page {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding-bottom: 10px;
  }
  
  .left-section {
    text-align: center;
    flex: 0.6 1;
    padding: 150px;
    background-color: #ffffff;
  }
  
  .left-section h3 {
    font-weight: normal;
    font-family: 'Ledger', serif;
    font-size: 50px;
    color: #5280D8;
  }
  h3 span{
    font-weight: normal;
    font-family: 'Ledger', serif;
    color: #000;
  }
  
  .left-section h1 span {
    color: black;
  }
  
  .left-section p {
    font-size: 20px;
    margin-top: 20px;
  }
  
  .right-section {
    border-radius: 20px;
    flex: 1;
    padding: 50px;
    background-color: white;
    border-left: 1px solid #ddd;
    margin-right: 60px;
  }
  
  .right-section h2 {
    font-size: 32px;
    margin-bottom: 20px;
    color: #5280D8;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .form-group textarea {
    height: 100px;
  }
  
  .submit-button {
    background-color: #5280D8;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .submit-button:hover {
    background-color: #45a049;
    
  }
  